.App {
  position: relative;
  text-align: center;
  align-self: center;
  height: 100vh;
  background-color: black;
}

.App>div {
  width: 90vmin;
  height: 90vmin;
  /* background-color: gray; */
  /* border: 1px dashed gray; */

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-wrap: wrap;
  gap: 11%;
}

.block {
  width: 26%;
  height: 26%;

  position: relative;
}

.block-flex {
  width: 100%;
  height: 100%;

  display: flex;
  flex-wrap: wrap;
  gap: 8%;
}

.block-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
}

.block-overlay>span {
  display: block;
  margin-top: 50%;
  transform: translate(0, -50%);
  font-size: 3vmin;
  font-weight: bold;
}

.dot {
  width: 19%;
  height: 19%;
}
